import { React, useState } from "react";
import { Link, withRouter } from "react-router-dom";

import '../styles/styles.css';

import pumpkinspiceroll from '../images/pumpkinspice.jpg';
import pumpkinspiceroll_sugar from '../images/pumpkinspice-sugar.jpg';
import pumpkinspiceroll_vanilla from '../images/pumpkinspice-vanilla.jpg';
import pumpkinspiceroll_chocolate from '../images/pumpkinspice-chocolate.jpg';

function PumpkinSpice(props) {
  const [glaze, setGlaze] = useState("None");
  const [quantity, setQuantity] = useState(1);

  return (
    <div id="pumpkinspice-container">
      <body className="standard">

        <div className="item_grid">
          <div className="item_image">
            <img className={glaze === "None" ? "roll_image" : "notselectedimage" } src={pumpkinspiceroll} alt="pumpkinspiceroll"/>
            <img className={glaze === "Sugar" ? "roll_image" : "notselectedimage" } src={pumpkinspiceroll_sugar} alt="pumpkinspiceroll_sugar"/>
            <img className={glaze === "Vanilla" ? "roll_image" : "notselectedimage" } src={pumpkinspiceroll_vanilla} alt="pumpkinspiceroll_vanilla"/>
            <img className={glaze === "Chocolate" ? "roll_image" : "notselectedimage" } src={pumpkinspiceroll_chocolate} alt="pumpkinspiceroll_chocolate"/>

            <p className="ingredients"><strong>Ingredients:</strong> Sugar, Water, Flour, Pumpkin Puree, Vanilla Extract, Eggs</p>

            <p className={glaze === "Sugar" ? "ingredients" : "notselectedingredients" }><strong>Glaze: </strong>Butter, Milk, Powdered Sugar</p>
            <p className={glaze === "Vanilla" ? "ingredients" : "notselectedingredients" }><strong>Glaze: </strong>Butter, Vanilla Bean Paste, Milk, Powdered Sugar</p>
            <p className={glaze === "Chocolate" ? "ingredients" : "notselectedingredients" }><strong>Glaze: </strong>Butter, Milk, Cacao Powder, Powdered Sugar</p>
          </div>

          <div className="item_description">
            <h1>Pumpkin Spice Roll</h1>
            <p id="price">$3.99/roll</p>
            <p id="description">Our pumpkin spice rolls have rolled right in time for the fall season. Made with our house pumpkin spice seasoning, you’ll be wanting these year round! </p>

            <div className="buttons">
                <button className={glaze === "None" ? "selectedbutton" : "button" } onClick={() => {setGlaze("None"); console.log(glaze)}}>None</button>
                <button className={glaze === "Sugar" ? "selectedbutton" : "button" } onClick={() => {setGlaze("Sugar"); console.log(glaze)}}>Sugar</button>
                <button className={glaze === "Vanilla" ? "selectedbutton" : "button" } onClick={() => {setGlaze("Vanilla"); console.log(glaze)}}>Vanilla</button>
                <button className={glaze === "Chocolate" ? "selectedbutton" : "button" } onClick={() => {setGlaze("Chocolate"); console.log(glaze)}}>Chocolate</button>
            </div>

            <label htmlFor="Quantity">Quantity: &nbsp;</label>
            <select name="Quantity" id="quantity" onChange={(e) => {setQuantity(e.target.value); console.log(quantity)}}>
              <option value="1">1</option>
              <option value="3">3</option>
              <option value="6">6</option>
              <option value="12">12</option>
            </select>

            <button id="addcart" onClick={() => {props.onAddToCart(glaze, quantity); console.log(glaze, quantity)}}>Add to Cart</button>

          </div>
        </div>
      </body>
    </div>
  );
}
 export default PumpkinSpice;
