import { React, useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Navigation from "./components/Navigation.js";
import Home from "./components/Home.js";
import Products from "./components/Products.js";
import OurStory from "./components/OurStory.js";
import PumpkinSpice from "./components/PumpkinSpice.js";
import GlutenFree from "./components/GlutenFree.js";
import OriginalCinnamon from "./components/OriginalCinnamon.js";
import Walnut from "./components/Walnut.js";
import Blackberry from "./components/Blackberry.js";
import CaramelPecan from "./components/CaramelPecan.js";
import Cart from "./components/Cart.js";

import './styles/styles.css';

function App() {
  let shoppingCart = [];
  const [total, setTotal] = useState(0);

  let AddToCart = function(glaze, quantity) {
    console.log("Adding to cart");

    shoppingCart.push({
      glaze: glaze,
      quantity: quantity,
      itemid: ""
    })

    setTotal(total + parseInt(quantity));

    console.log("Added!", shoppingCart);
    console.log ("Cart Total: ", total);
  }

  return (
    <div className="App">
      <Router>
        <Navigation cart_total={total}/>
        <Switch>
          <Route path="/" exact component={() => <Home />} />
          <Route path="/ourstory" exact component={() => <OurStory />} />
          <Route path="/products" exact component={() => <Products />} />
          <Route path="/pumpkinspice" exact component={() => <PumpkinSpice onAddToCart={AddToCart}/>} />
          <Route path="/glutenfree" exact component={() => <GlutenFree />} />
          <Route path="/originalcinnamon" exact component={() => <OriginalCinnamon />} />
          <Route path="/walnut" exact component={() => <Walnut />} />
          <Route path="/blackberry" exact component={() => <Blackberry />} />
          <Route path="/caramelpecan" exact component={() => <CaramelPecan />} />
          <Route path="/cart" exact component={() => <Cart cart_total={total}/>} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
